* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

.App {
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.sheet {
  width: 60%;
  min-width: 30rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 3rem;
  margin-top: 3rem;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 1.5rem;
}

.box {
  margin-top: 5px;
  display: flex;
  flex-direction: column;
  width: 50vw;
  min-width: 25rem;
}

.mr10 {
  border: none;
  width: 100%;
}

.mr10::placeholder {
  text-align: left;
}

input {
  padding: 5px 10px;
}

.btn-save {
  width: 100%;
  min-width: 25rem;
  height: 2rem;
  border: none;
  border-radius: 5px;
  text-align: center;
  background-color: rgb(180, 189, 192);
  font-weight: bold;
  font-size: 1rem;
  margin-top: 1rem;
}
.btn-save:hover {
  background-color: rgb(131, 137, 141);
}

.print {
  width: 50vw;
  min-width: 25rem;
  height: 2rem;
  border: none;
  border-radius: 5px;
  text-align: center;
  background-color: rgb(180, 189, 192);
  font-weight: bold;
  font-size: 1rem;
  margin-top: 20px;
  margin-bottom: 20px;
}
.print:hover {
  background-color: rgb(131, 137, 141);
}

.btn-add {
  width: 50vw;
  min-width: 25rem;
  height: 2rem;
  border: none;
  border-radius: 5px;
  text-align: center;
  background-color: rgb(180, 189, 192);
  font-weight: bold;
  font-size: 1rem;
  margin-top: 20px;
}
.btn-add:hover {
  background-color: rgb(131, 137, 141);
}

.btn-remove {
  min-width: 5rem;
  height: 2rem;
  border: none;
  border-radius: 5px;
  text-align: center;
  background-color: rgba(218, 30, 30, 0.952);
  font-weight: bold;
  font-size: 1rem;
  margin-top: 20px;
}
.btn-remove:hover {
  background-color: rgb(182, 9, 3);
}

.btn-edit {
  width: 50vw;
  min-width: 25rem;
  height: 2rem;
  border: none;
  border-radius: 5px;
  text-align: center;
  background-color: rgba(196, 196, 196, 0.952);
  font-weight: bold;
  font-size: 1rem;
  margin-top: 20px;
  margin-bottom: 15px;
}
.btn-edit:hover {
  background-color: rgb(55, 174, 253);
}

.textSection {
  min-width: 5rem;
  text-align: left;
  font-size: 1.5rem;
  line-height: 2rem;
}

.categoryText {
  font-size: 1.5rem;
  margin-right: 1.5px;
  font-weight: bold;
  line-height: 2rem;
}

h2 {
  font-size: 2rem;
  color: rgb(218, 30, 30, 0.952);
  min-width: 5rem;
  margin-top: 1rem;
}

hr {
  border: solid 1px;
}

.main-head {
  width: 100% !important;
  background-color: aquamarine;
}
